<template>
  <div>
    <fTitle>
      <template #title>Empty 空状态</template>
      <template #introduce>空状态时的占位提示。</template>
    </fTitle>
    <sTitle>
      <template #title>基础用法</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="date"
        label="日期"
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="address"
        label="地址">
      </el-table-column>
      <template slot="empty">
        <el-empty description="当前无数据"></el-empty>
        </template>
        <!-- 原本默认无数据为一段文字，这里用命名插槽可以修改默认字段 -->
    </el-table>
    <el-button plain @click="addData">添加数据</el-button>
</div>
    </el-col></el-row>
    </div></div>
    <el-dialog style="text-align: start;" width="600px" :title="'添加新的数据'" :visible.sync="dialogFormVisible">
  <el-form :model="form">
    <el-form-item label="输入姓名">
      <el-input v-model="form.name" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="输入地址">
      <el-input v-model="form.address" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="日期">
      <el-input v-model="form.date" autocomplete="off"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="sureChange">确 定</el-button>
  </div>
</el-dialog>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      dialogFormVisible: false,
      form: {
        date: '',
        name: '',
        address: ''
      },
      tableData: []
    }
  },
  methods: {
    addData () {
      this.dialogFormVisible = true
    },
    sureChange () {
      this.tableData.push(this.form)
      this.form = {
        date: '',
        name: '',
        address: ''
      }
      this.dialogFormVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;}
    }}
</style>
